import React from "react";
import "./Login.css";
import { Link } from "react-router-dom";

export default function Login() {
  return (
    <div>
      <form
        id="lform"
        class="form-signin"
        name="validform"
        onsubmit="return validatePassword()"
      >
        <h1 class="h3 mb-3 font-weight-normal">Please sign in</h1>
        <label for="inputEmail" class="sr-only">
          Email address
        </label>
        <input
          type="email"
          id="inputEmail"
          class="form-control"
          placeholder="Email address"
          autofocus
          required
        />
        <label for="inputPassword" class="sr-only">
          Password
        </label>
        <input
          type="password"
          id="inputPassword"
          class="form-control"
          placeholder="Password"
          pattern=".{3,}"
          required
        />
        <Link className="link" to="/Show">
          <button
            type="submit"
            class="btn btn-lg btn-primary btn-block btn-outline-success"
          >
            Login
          </button>
        </Link>
      </form>
    </div>
  );
}
